import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-enable-section',
})
export class OrderEnableection {

    @Prop() heading: string;
    @Prop() name: string;
    @Prop() value: string;

    @Element() root: HTMLElement;

    @Prop({mutable: true, reflect: true}) active: boolean = false;
    ChangeHandler() {
        const input = this.root.querySelector(`div > label > input`) as HTMLInputElement;
        this.active = input.checked;
    }

    componentDidLoad(){
        this.root.style.display = "block";
        this.ChangeHandler();
    }

    render() {
        return (
            <div>
                <label class="uk-display-block uk-margin">
                    <input type="checkbox" class="uk-checkbox" name={this.name} value={this.value}
                        onChange={()=>this.ChangeHandler()}></input>

                    <span> {this.heading}</span>
                </label>

                <div class="inputs" hidden={!this.active}>
                    <slot />
                </div>
                
            </div>
        )
    }
}